import { AppBar, Toolbar, Typography, Menu, MenuItem } from "@material-ui/core";
import { Add, Apps } from "@material-ui/icons";
import Avatar from "@mui/material/Avatar";
import React from "react";
import { useStyles } from "./style";
import CreateClass from "../Drawer/Createclass/Createclass";
import { useLocalContext } from "../../Context/Context";
import { JoinClass } from "..";
import { auth } from "./../../lib/firebase";
import logo from "./../../asset/logo.ico";

const Header = ({ children }) => {
  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);
  const { setCreateClassDialog, setjoinclassdialog, loggedInUser } =
    useLocalContext();
  const logout = () => {
    auth.signOut();
    window.location = "/signin";
  };

  const redirect = (event) => {
    event.preventDefault();
    window.location = "/";
  };

  const handleCreate = () => {
    handleClose();
    setCreateClassDialog(true);
  };
  const handleJoin = () => {
    handleClose();
    setjoinclassdialog(true);
  };
  return (
    <div className={classes.root}>
      <AppBar
        style={{ background: "#59b4bb" }}
        className={classes.appbar}
        position="static"
      >
        <Toolbar className={classes.toolbar}>
          <div className={classes.headerWrapper}>
            {children}
            <img className={classes.logo} src={logo} alt="" />

            <Typography
              onClick={redirect}
              variant="h6"
              className={classes.title}
            >
              Study Buddy
            </Typography>
          </div>
          <div className={classes.header__wrapper__right}>
            <Add onClick={handleClick} className={classes.icon} />
            <Apps className={classes.icon} />
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleCreate}>Create Class</MenuItem>
              <MenuItem onClick={handleJoin}>Join Class</MenuItem>
            </Menu>
            <div>
              <Avatar
                onClick={logout}
                src={loggedInUser?.photoURL}
                className={classes.icon}
              />
            </div>
          </div>
        </Toolbar>
      </AppBar>
      <CreateClass />
      <JoinClass />
    </div>
  );
};

export default Header;
